<template>
  <div class="history">

    <b-alert show>
      <b-container>
        <b-row>
          <b-col md class="last-check">
            <span class="last-check-label clickable" @click="showFiles = !showFiles" v-bind:class="showFiles ? 'bold' : ''">
              Files Scanned Last:
            </span> <i>{{lastFileScan | formatDateTime}}</i>
            <span class="clickable" @click="deleteHistory({type: 'files'})">
              <delete-icon title="Delete all but latest entry"/>
            </span>
          </b-col>
          <b-col md class="last-check">
            <span class="last-check-label clickable" @click="showDB = !showDB" v-bind:class="showDB ? 'bold' : ''">
              DB Refreshed Last:
            </span> <i>{{lastUpdateDB | formatDateTime}}</i>
            <span class="clickable" @click="deleteHistory({type: 'db'})">
              <delete-icon title="Delete all but latest entry"/>
            </span>
          </b-col>
        </b-row>
      </b-container>
    </b-alert>

    <b-card v-for="event in historyFiltered" v-bind:key="event.datetime" style="margin: 10px;"
        :sub-title="event.datetime | formatDateTime" :title="event.type === 'files' ? 'Files Found' : (event.type === 'db' ? 'DBs Updated' : '')">

      <!--<p class="card-text">-->
        <!--{{event.games.length}} Game(s):-->
      <!--</p>-->
      <div>
        <ul style="text-align: left;">
          <li v-for="game in event.games" class="clickable" :title="game.id">
            <GameModalLink v-bind:game-id="game.id">
              {{game.name || game.fileNameTitle || game.id}}
              <b-badge v-if="game.wasNew" variant="success">New</b-badge>
              <b-badge v-if="game.wasUnlocked" variant="dark">Unlocked</b-badge>
              <b-badge v-if="game.wasKeyFound" variant="dark">Key</b-badge>
              <b-badge v-if="game.wasKeyChanged" variant="warning">Key Changed</b-badge>
              <b-badge v-if="game.wasKeyRemoved" variant="warning">Key Removed</b-badge>
              <b-badge v-if="game.wasUpdated" variant="primary" :title="game.wasUpdated">Updated v{{game.wasUpdated | humanizeVer}}</b-badge>
              <ul>
                <li v-for="dlc in game.dlcs" :title="dlc.id">
                  {{dlc.name || dlc.fileNameTitle || dlc.id}}
                  <b-badge v-if="dlc.wasNew" variant="success">New</b-badge>
                  <b-badge v-if="dlc.wasUnlocked" variant="dark">Unlocked</b-badge>
                  <b-badge v-if="dlc.wasKeyFound" variant="dark">Key</b-badge>
                  <b-badge v-if="dlc.wasKeyChanged" variant="warning">Key Changed</b-badge>
                  <b-badge v-if="dlc.wasKeyRemoved" variant="warning">Key Removed</b-badge>
                  <b-badge v-if="dlc.wasUpdated" variant="primary" :title="dlc.wasUpdated">Updated v{{dlc.wasUpdated | humanizeVer}}</b-badge>
                </li>
              </ul>
            </GameModalLink>
          </li>
        </ul>
      </div>
    </b-card>

  </div>
</template>

<script>
// @ is an alias to /src
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import {mapActions} from 'vuex'
import DeleteIcon from 'vue-material-design-icons/Delete'
import GameModalLink from '@/components/GameModalLink'

export default {
  name: 'history',
  components: {GameModalLink, DeleteIcon},
  data() {
    return {
      showDB: true,
      showFiles: true
    }
  },
  created() {
    this.$store.dispatch('fetchHistory')
    this.$store.dispatch('fetchGamesData')
  },
  computed: {
    ...mapState(['history', 'lastFileScan', 'lastUpdateDB']),
    historyFiltered() {
      let history = this.history
      if (!this.showDB) {
        history = _.reject(history, {type: 'db'})
      }
      if (!this.showFiles) {
        history = _.reject(history, {type: 'files'})
      }
      return history
    }
  },
  methods: {
    ...mapActions(['deleteHistory'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.last-check {
  .last-check-label {
    margin: 0 5px;
  }
  .bold {
    font-weight: bold;
  }
  .delete-icon {
    margin-left: 5px;
  }
}
</style>
